.menu {
    text-transform: uppercase;
    font-size: 10px;
    margin-bottom: rem(30);

    
    display: flex;
    justify-content: space-around;

    // div:not(:last-child) {
      
    // }

    &__item {
        cursor: pointer;
        color: grey;
        font-weight: 600;

        &--active {
            border-bottom: 2px solid black;
            color: black;
        }
    }
}
@include breakpoint(s){
    .menu{
        font-size: 16px;
    }
}