  text-transform: uppercase;
}

.program {
  display: grid;
  grid-template-columns: auto auto;
}

h1, h2, h3 {
  font-weight: 700;
  margin-top: 0;
}

h1 {
  font-size: clamp(1.5rem, 0.875rem + 2vw, 2.75rem);
}

p {
  margin-top: 0;
}

a, a:visited, active {
  text-decoration: none;
