  background-color: #312d2b;
  min-height: 45.625rem;
  padding-top: 7.5rem;
  padding-bottom: 7.5rem;
}

.grid {
  display: grid;
  grid-template-rows: 9.375rem 9.375rem;
  gap: 1.875rem;
  grid-template-columns: 50% 55%;
  grid-template-areas: "item1 item2 " "item1 item2" "item3 item4";
}
.grid img {
  width: 100%;
  height: 100%;
  object-fit: fit;
}

.item1 {
  grid-area: item1;
}

.item2 {
  grid-area: item2;
}

.item3 {
  grid-area: item3;
}

.item4 {
  grid-area: item4;
}

@media (min-width: 42.5em) {
  .grid {
    grid-template-rows: 15.3125rem 15.3125rem;
    gap: 1.875rem;
    grid-template-columns: 24% 54% 24%;
    grid-template-areas: "item1 item2 item3" "item1 item2 item4";
  }
